// Emoji Helper -  '  (  ,  )
import openmoji from '../assets/openmoji.json';
import { emojiList } from './emojiList';

//     EmojiItem  ( name)
export function getAvailableEmojis(count: number) {
  //    emojiList   
  const shuffled = emojiList.slice().sort(() => Math.random() - 0.5);
  return shuffled.slice(0, count);
}
